<template>
  <div class="new-group-container">
    <div style="display: flex; justify-content: flex-end;">
      <el-button
        type="primary"
        style="background: #0f3959; border-color: #0f3959;"
        icon="el-icon-plus"
        >New Group</el-button
      >
    </div>

    <div class="new-group-card">
      <div class="ngc-title">
        New Group
      </div>
      <div class="ngc-body">
        <div style="flex: 2">
          <div class="input-group">
            <span class="input-group-label">Group Name*</span>
            <input class="input-group-input" type="text" v-model="name" />
          </div>

          <ul class="icon-list">
            <drop
              v-for="(activeIcon, index) in activeIcons"
              :key="`active-icon-${index}`"
              class="drop"
              @drop="handleDrop"
              @dragover="handleDragover(index)"
            >
              <drag
                :transfer-data="activeIcon"
                @dragstart="dragStartIndex = index"
              >
                <li>
                  <el-popover trigger="hover" placement="top">
                    <p>Name: {{ activeIcon.name }}</p>
                    <p>Program location: ***</p>
                    <div
                      slot="reference"
                      class="name-wrapper"
                      v-if="activeIcon.filePath"
                    >
                      <img :src="activeIcon.filePath | filePath" alt="" />
                    </div>
                  </el-popover>
                </li>
              </drag>
            </drop>
          </ul>

          <div class="ngc-buttons">
            <div class="ngc-button btn-computers" @click="openComputersDialog">
              Computers
            </div>
            <div
              class="ngc-button btn-background"
              @click="openBackgroundDialog"
            >
              Background
            </div>
            <div class="ngc-button btn-save" @click="onClickSave">
              Save
            </div>
            <div class="ngc-button btn-cancel" @click="onClickCancel">
              Cancel
            </div>
          </div>
        </div>
        <div style="flex: 1; margin-left: 30px">
          <span class="input-group-label">Icon List</span>
          <drop @drop="handleDropIconList">
            <div class="icon-area">
              <div v-for="(icon, index) in iconList" :key="`icon-${index}`">
                <drag
                  class="drag"
                  :transfer-data="icon"
                  @dragstart="dragStartIndex = -1"
                >
                  <el-tooltip
                    class="icon-image"
                    :content="icon.name"
                    placement="right"
                  >
                    <img :src="icon.filePath | filePath" alt="" />
                  </el-tooltip>
                </drag>
              </div>
            </div>
          </drop>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Drag, Drop } from "vue-drag-drop";
export default {
  components: { Drag, Drop },
  data() {
    return {
      activeIcons: Array(12).fill(""),
      dropIndex: -1,
      iconList: [],
      name: "",
    };
  },
  methods: {
    handleDragover(data) {
      this.dropIndex = data;
    },
    handleDrop(data) {
      let tempIcons = { ...this.activeIcons };
      let isWarning = false;
      if (this.activeIcons[this.dropIndex] == "") {
        tempIcons[this.dropIndex] = data;
        if (this.dragStartIndex > -1) {
          tempIcons[this.dragStartIndex] = "";
        }
      } else {
        if (this.dragStartIndex > -1) {
          const tmpIcn = tempIcons[this.dragStartIndex];
          tempIcons[this.dragStartIndex] = tempIcons[this.dropIndex];
          tempIcons[this.dropIndex] = tmpIcn;
        } else {
          this.$notify({
            title: "Warning",
            message: "Please place icon to the other area",
            type: "warning",
          });
          isWarning = true;
        }
      }
      this.activeIcons = tempIcons;

      // Remove droped Icon from IconList
      if (!isWarning) {
        const newIconList = this.iconList.filter((icon) => {
          return icon._id != data._id;
        });
        this.iconList = newIconList;
      }
    },
    getImage(imageName) {
      var images = require.context("../assets/image/", false, /\.png$/);
      return images(`./${imageName}`);
    },
    openComputersDialog() {
      this.$store.commit("setComputerDialogVisibility", true);
    },
    handleDropIconList(data) {
      let isUpdated = false;
      let newActiveIcons = {};
      for (var i = 0; i < 12; i++) {
        let icon = this.activeIcons[i];
        if (icon._id && icon._id == data._id) {
          icon = "";
          isUpdated = true;
        }
        newActiveIcons[i] = icon;
      }

      if (isUpdated) {
        this.activeIcons = newActiveIcons;
        this.iconList.push(data);
      }
    },
    openBackgroundDialog() {
      this.$store.commit("setBackgroundDialogVisibility", true);
    },
    onClickSave() {
      const group = {
        name: this.name,
        activeIcons: this.activeIcons,
      };
      this.$store.dispatch("group/create", {
        group: group,
        notify: this.$notify,
      });
      this.$router.push("/groups");
    },
    onClickCancel() {
      this.$router.push("/groups");
    },
  },
  computed: {
    icons() {
      return this.$store.state.icon.list;
    },
  },
  mounted() {
    const payload = { notify: this.$notify };
    this.$store.dispatch("icon/loadList", payload);
    this.$store.commit("group/setActiveComputers", []);
  },
  watch: {
    icons() {
      this.iconList = this.icons;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/new-group.scss";
@import "src/assets/scss/users.scss";
@import "src/assets/scss/login.scss";
</style>
